<template>
<div class="testApp">
  <img src="./assets/fire.png" style="width: 200px;">

  <h1>퇴사 테스트</h1>
  <p>"회사 다니기 싫은데.. 나는 퇴사해야 할까?"</p>
  <hr style="margin-left:50px; margin-right:50px; margin-bottom: 20px;">
<MainCompoent v-if="page==0" :page="page" @changePage="page=1"></MainCompoent>
  <TestComponent v-if="page==1" :page="page" :questionList="questionList"
  @changePage="page=2" @questionAdd="questionAdd"></TestComponent>
  <ResultComponent v-if="page==2" :page="page" :total="total" :baby="baby" :marry="marry"
  :parent="parent"
  @changePage="page=0;question=[];total=0;marry=0;baby=0;parent=0;"></ResultComponent>
</div>
</template>

<script>
import MainCompoent from './components/MainComponent.vue'
import TestComponent from './components/TestComponent.vue'
import ResultComponent from './components/ResultComponent.vue'
import questionList from './assets/questionList'

export default {
  name: 'App',
  components: {
    MainCompoent,
    TestComponent,
    ResultComponent,
  },
  data(){
    return{
      page:0,
      question:[],
      questionList:questionList,
      total:0,
      marry:0,
      baby:0,
      parent:0,
    }
  },
  methods:{
    questionAdd(a){
      this.question.push(a[0]);
      // console.log(a,a[0],a[1]);
      // console.log("",this.question);
      if(a[0]==1 && a[1]==1){
        // 결혼
        this.total-=5;
        this.marry = 1;

      }else if(a[0]==1 && a[1]==2){
        // 아이
        this.total-=10;
        this.baby =1;

      }else if(a[0]==1 && a[1]==3){
        // 물려받을게 있음
        this.total+=40;
        this.parent = 1;
      }else if(a[0]==1 && a[1]==4){
        // 집 물려줌
        this.total+=7;
      }else if(a[0]==1 && a[1]==5){
        // 부모님 지원받음
        this.total+=5;
}
      else{
        this.total+=a[0];
      }
      // console.log(this.total);
    },
  }
}
</script>

