<template>
  <div>
    <div>
    <!-- 25점인 이상인경우 -->
    <div v-if="total>=25">
      <h4>[혹시 회사는 취미로?]</h4>
      <p>"서민 체험중이신가요? 밖에서 고생하시지 마시고
        집으로 돌아오세요 도련님...! 아가씨...!!"</p>
    </div>
    <!-- 19점이상 25점 이하인 경우 -->
    <div v-if="total>=19 && total <=25">
      <h4>[저축할 필요도 없으시겠네요]</h4>
      <p>"이상적인 경제 생활을 하고 계시네요 대부분의 서민들은
        집과 차를 사기위해 저금을 한다고 합니다. 선생님은
        저축할 필요없는 금수저일 확률이 높습니다. 퇴사는 당신의 선택"</p>
    </div>
    <!-- 16점이상 18점이하 -->
    <div v-if="total>=16 && total <=18">
      <h4>[아쉬울게 없는사람]</h4>
      <p>"충분히 어디가서 돈으로 아쉬워 할 일 없는 분이신거 같네요
        가끔씩 퇴사하고 놀러가셔도 될거같네요"</p>
    </div>
    <!-- 11점이상 15점이하인경우 -->
    <div v-if="total>=11 && total <=15">
      <h4>[재밌게 다녀보세요]</h4>
      <p>"상위권 직장에서 행복하게 일하시는군요 부럽습니다! 재미로 테스트 보신거죠?
        그래도 퇴사할 정돈 아니시네요"</p>
    </div>
    <!-- 8-10점인경우 -->
    <div v-if="total>=8 && total<=10">
      <h4>[좋은 직장에 다니시네요]</h4>
      <p>"불만이 다소 있으시지만 평균 이상의 직장을 다니시고 계십니다. 그냥 다니세요"</p>
    </div>
    <!-- 4-7점인경우 -->
    <div v-if="total>=4 && total<=7">
      <h4>[평범하시네요]</h4>
      <p>"본인이 가장 힘들다고 생각 하시겠지만... 이정도면
        직장인 평균일거에요 열심히 다니세요!"</p>
    </div>
    <!-- 0-3점인경우 -->
    <div v-if="total>=0 && total<=3">
      <h4>[힘드시겠네요]</h4>
      
      <p>"힘드시겠지만.. 그래도 열심히 일해봅시다. 퇴사는 하지마세요"</p>
    </div>
        <!-- -5점에서 0점 경우 -->
        <div v-if="total>=-5 && total<0">
      <h4>[이 테스트는 왜 하신거에요?]</h4>
      
      <p>"그만두시면 안되는거 아시잖아요.. 그저 상상으로 만족해 봅시다."</p>
    </div>
        <!-- -5점 이하 경우 -->
        <div v-if="total<-5">
      <h4>[안됩니다]</h4>
      
      <p>"당신도 알고 있어요.. 당신은 퇴사할 수 없다는걸
        상상할 자격도 없습니다."</p>
    </div>
    <!-- 결혼,애기,물려받은거 여부 -->
    <br>
    <div v-if="marry===1 && parent===0"><p>[당신의 배우자가 말합니다]</p>
      <p>"이번달도 돈나갈곳이 많은데.."</p>
    </div>
    <div v-if="baby===1 && parent===0"><p>[당신의 아이가 이야기 하네요]</p>
      <p>"응애..응애!! 퇴사해?"</p>
    </div>
    <div v-if="parent===1">
      <p>[당신의 부모님이 말씀하시네요]</p>
      <p>"회사는 취미로 다니렴"</p></div>
    <!-- {{ marry }}{{ baby }}{{ parent }} -->

  </div>

    <br>
    <button class="main-button" @click="$emit('changePage',0)">테스트 다시 하기</button>
    <hr style="margin-left:50px; margin-right:50px;">
    <br>
    <p>"다른 사람에게도 추천해 주고 싶다면?"</p>
    <button class="kakao-share" @click="kakaoShare">카카오 공유</button>&nbsp;
<input type="text" id="text" value="https://colabear92.github.io/fire" style="display: none;"/>
<button class="main-button" @click="urlCopy">🔗링크 복사</button>
<br>
<a style="text-decoration: none; color:black;"
href="https://colabear92.github.io/"><button class="main-button">다른 테스트도 하고싶다면?</button></a>

  </div>
</template>

<script>
export default {
    data(){
    return{
      testurl:'dd',
    }
  },
    name: 'ResultComponent',
    props:{
    page:Number,
    total:Number,
    baby:Number,
    marry:Number,
    parent:Number,
  },
  methods:{
    kakaoShare() {
      window.Kakao.Share.sendCustom({
        templateId: 93343 ,
        // 카카오톡이 설치 되지 않았을때 마켓으로 이동
        installTalk: true
      });},
      urlCopy(){
        var url = document.getElementById('text');
        url.style.display='block';    // 숨겨둔 input 태그 block처리
        url.select();    // 복사할 text 블럭
        document.execCommand('copy');    // 드레그된 text 클립보드에 복사
        url.style.display='none';    // 다시 숨기기
        alert('url이 복사 되었습니다.')
      }
  }
}
</script>

